
import { Tuple } from '.';


// AccountStatusCode represents the account status: ACTIVE | INACTIVE | BLOCKED ...
export enum AccountStatusCode {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // Active account in the system [1] 
    ACTIVE = 1,
 
    // Inactive account in the system [2] 
    INACTIVE = 2,
 
    // Blocked account [3] 
    BLOCKED = 3,
 
    // Suspended account (about to be deleted) [4] 
    SUSPENDED = 4,
 
}

// Return list of AccountStatusCode values and their display names
export function GetAccountStatusCodes() : Tuple<AccountStatusCode, string>[] {
	let result : Tuple<AccountStatusCode, string>[] = [];

    
	result.push(new Tuple<AccountStatusCode, string>(AccountStatusCode.ACTIVE, 'AccountStatusCode.ACTIVE'))
	
	result.push(new Tuple<AccountStatusCode, string>(AccountStatusCode.INACTIVE, 'AccountStatusCode.INACTIVE'))
	
	result.push(new Tuple<AccountStatusCode, string>(AccountStatusCode.BLOCKED, 'AccountStatusCode.BLOCKED'))
	
	result.push(new Tuple<AccountStatusCode, string>(AccountStatusCode.SUSPENDED, 'AccountStatusCode.SUSPENDED'))
	

    return result;
}

// Return map of AccountStatusCode values and their display names
export function MapAccountStatusCodes() : Map<AccountStatusCode, string> {
    let result = new Map<AccountStatusCode, string>();

    
	result.set(AccountStatusCode.UNDEFINED, 'Undefined');
	
	result.set(AccountStatusCode.ACTIVE, 'Active');
	
	result.set(AccountStatusCode.INACTIVE, 'Inactive');
	
	result.set(AccountStatusCode.BLOCKED, 'Blocked');
	
	result.set(AccountStatusCode.SUSPENDED, 'Suspended');
	

    return result;
}
