
import { Tuple } from '.';


// LeadStatusCode represents the lead status: NEW | INPROCESS | ASSIGNED ...
export enum LeadStatusCode {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // New Lead [1] 
    NEW = 1,
 
    // Lead in process [2] 
    INPROCESS = 2,
 
    // Lead assigned [3] 
    ASSIGNED = 3,
 
    // Lead converted [4] 
    CONVERTED = 4,
 
    // Lead irrelevant [5] 
    IRRELEVANT = 5,
 
    // Lead redirected [6] 
    REDIRECTED = 6,
 
    // Lead recycled [7] 
    RECYCLED = 7,
 
    // Lead is no longer exists [8] 
    DEAD = 8,
 
}

// Return list of LeadStatusCode values and their display names
export function GetLeadStatusCodes() : Tuple<LeadStatusCode, string>[] {
	let result : Tuple<LeadStatusCode, string>[] = [];

    
	result.push(new Tuple<LeadStatusCode, string>(LeadStatusCode.NEW, 'LeadStatusCode.NEW'))
	
	result.push(new Tuple<LeadStatusCode, string>(LeadStatusCode.INPROCESS, 'LeadStatusCode.INPROCESS'))
	
	result.push(new Tuple<LeadStatusCode, string>(LeadStatusCode.ASSIGNED, 'LeadStatusCode.ASSIGNED'))
	
	result.push(new Tuple<LeadStatusCode, string>(LeadStatusCode.CONVERTED, 'LeadStatusCode.CONVERTED'))
	
	result.push(new Tuple<LeadStatusCode, string>(LeadStatusCode.IRRELEVANT, 'LeadStatusCode.IRRELEVANT'))
	
	result.push(new Tuple<LeadStatusCode, string>(LeadStatusCode.REDIRECTED, 'LeadStatusCode.REDIRECTED'))
	
	result.push(new Tuple<LeadStatusCode, string>(LeadStatusCode.RECYCLED, 'LeadStatusCode.RECYCLED'))
	
	result.push(new Tuple<LeadStatusCode, string>(LeadStatusCode.DEAD, 'LeadStatusCode.DEAD'))
	

    return result;
}

// Return map of LeadStatusCode values and their display names
export function MapLeadStatusCodes() : Map<LeadStatusCode, string> {
    let result = new Map<LeadStatusCode, string>();

    
	result.set(LeadStatusCode.UNDEFINED, 'Undefined');
	
	result.set(LeadStatusCode.NEW, 'New');
	
	result.set(LeadStatusCode.INPROCESS, 'Inprocess');
	
	result.set(LeadStatusCode.ASSIGNED, 'Assigned');
	
	result.set(LeadStatusCode.CONVERTED, 'Converted');
	
	result.set(LeadStatusCode.IRRELEVANT, 'Irrelevant');
	
	result.set(LeadStatusCode.REDIRECTED, 'Redirected');
	
	result.set(LeadStatusCode.RECYCLED, 'Recycled');
	
	result.set(LeadStatusCode.DEAD, 'Dead');
	

    return result;
}
