
import { Injectable, Inject } from '@angular/core';
import { RestUtil, EntityResponse, EntitiesResponse, ActionResponse } from '../utils';
import { GooxConfig } from '../config';

import { Campaign } from '.';



// CampaignsEndPoint Services for campaigns actions 
@Injectable()
export class CampaignsEndPoint {

  // URL to web api
  private baseUrl = '/campaigns';

  // Class constructor
  constructor(@Inject('config') private config: GooxConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Create new campaign
   */
  create(body?: Campaign) {
    return this.rest.post<EntityResponse<Campaign>>(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Update existing campaign
   */
  update(body?: Campaign) {
    return this.rest.put<EntityResponse<Campaign>>(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Delete campaign and all its content
   */
  delete(id?: string) {
    return this.rest.delete<ActionResponse>(`${this.baseUrl}/${id}`);
  }

  /**
   * Get a single campaign by id
   */
  get(id?: string) {
    return this.rest.get<EntityResponse<Campaign>>(`${this.baseUrl}/${id}`);
  }

  /**
   * Find campaigns by query
   */
  find(search?: string, sort?: string, page?: number, size?: number) {
    const params = [];		
    if (search != null) { params.push(`search=${search}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (size != null) { params.push(`size=${size}`); }

		return this.rest.get<EntitiesResponse<Campaign>>(`${this.baseUrl}`, ...params);
  }

}
