



// Address model represents an address
export class Address {
 
    // Street address 
    public street: string;
 
    // City 
    public city: string;
 
    // State (if applicable) 
    public state: string;
 
    // Local zip code (postal cod) 
    public zipCode: string;
 
    // Country name 
    public country: string;
 
     constructor(street?: string, city?: string, state?: string, zipCode?: string, country?: string) { 
        if (street !== undefined) { this.street = street; }
        if (city !== undefined) { this.city = city; }
        if (state !== undefined) { this.state = state; }
        if (zipCode !== undefined) { this.zipCode = zipCode; }
        if (country !== undefined) { this.country = country; }
    }

}



