
import { Tuple } from '.';


// AirplaneValidationFlag represents combination of airplane validations
export enum AirplaneValidationFlag {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // Valid airworthiness (isairplanevalidairworthiness_c) [1] 
    AIRWORTHINESS = 1,
 
    // Valid airplane insurance (isairplanevalidinsurance_c)  [2] 
    INSURANCE = 2,
 
    // Valid flight manual (isairplanevalidflightmanual_c) [4] 
    FLIGHT_MANUAL = 4,
 
    // Valid radio station license (isairplanevalidradiostation_c) [8] 
    RADIO_STATION = 8,
 
    // Certification Of Registration (isairplanevalidcertofregis_c)[16] 
    REGISTRATION_CERT = 16,
 
    // Valid first-aid kit (isairplanevalidfirstaidkit_c) [32] 
    FIRST_AID_KIT = 32,
 
    // Valid fire extinguisher (isairplanevalidfireextinguis_c) [64] 
    FIRE_EXTINGUISHER = 64,
 
    // Valid operation spec (isairplanevalidoperationspec_c) [128] 
    OPERATION_SPEC = 128,
 
    // Valid 337 certificate for camera hole (isairplanevalid337cameraho_c) [256] 
    VALID_337_CAMERA = 256,
 
    // Valid 337 certificate for exhaust (isairplanevalid337exhaust_c) [512] 
    VALID_337_EXHAUST = 512,
 
    // Valid stc camera hole (isairplanevalidstccameraho_c) [1024] 
    VALID_STC_CAMERA = 1024,
 
    // Valid stc certificate for exhaust (isairplanevalidstcexhaust_c) [2048] 
    VALID_STC_EXHAUST = 2048,
 
    // Valid flight logbook (isairplanevalidflightlogboo_c) [4096] 
    FLIGHT_LOGBOOK = 4096,
 
    // Valid air operator certificate AOC (isairplanevalidairoperatorce_c) [8192] 
    AIR_OPERATOR = 8192,
 
    // Aircraft weight certificate (isairplanevalidaircraftweigh_c) [16384] 
    AIRCRAFT_WEIGHT = 16384,
 
    // Valid propeller overhaul (isairplanevalidpropellerover_c) [32768] 
    PROPELLER_OVERHAUL = 32768,
 
    // Single engine overhaul (isairplanevalidsingleengineo_c) [65536] 
    SINGLE_ENGINE_OVERHAUL = 65536,
 
    // Left engine overhaul (isairplanevalidleftengineove_c) [131072] 
    LEFT_ENGINE_OVERHAUL = 131072,
 
    // Right engine overhaul (isairplanevalidrightengineov_c) [262144] 
    RIGHT_ENGINE_OVERHAUL = 262144,
 
    // Left engine propeller overhaul (isairplanevalidleftenginepro_c) [524288] 
    LEFT_PROPELLER_OVERHAUL = 524288,
 
    // Right engine propeller overhaul (isairplanevalidrightenginepr_c) [1048576] 
    RIGHT_PROPELLER_OVERHAUL = 1048576,
 
    // CVFR_ATS aeronautical maps (isairplanevalidcvfr_atsaeron_c) [2097152] 
    CVFR_ATS = 2097152,
 
    // Weight and Balance Form (isairplanevalidweightandbaln_c) [4194304] 
    WEIGHT_BALANCE_FORM = 4194304,
 
    // Airplane checklist (isairplanevalidairplanechec_c) [8388608] 
    CHECK_LIST = 8388608,
 
    // Noise Certificate (isairplanevalidnoisecertifi_c) [16777216] 
    NOISE_CERT = 16777216,
 
}

// Return list of AirplaneValidationFlag values and their display names
export function GetAirplaneValidationFlags() : Tuple<AirplaneValidationFlag, string>[] {
	let result : Tuple<AirplaneValidationFlag, string>[] = [];

    
	result.push(new Tuple<AirplaneValidationFlag, string>(AirplaneValidationFlag.AIRWORTHINESS, 'AirplaneValidationFlag.AIRWORTHINESS'))
	
	result.push(new Tuple<AirplaneValidationFlag, string>(AirplaneValidationFlag.INSURANCE, 'AirplaneValidationFlag.INSURANCE'))
	
	result.push(new Tuple<AirplaneValidationFlag, string>(AirplaneValidationFlag.FLIGHT_MANUAL, 'AirplaneValidationFlag.FLIGHT_MANUAL'))
	
	result.push(new Tuple<AirplaneValidationFlag, string>(AirplaneValidationFlag.RADIO_STATION, 'AirplaneValidationFlag.RADIO_STATION'))
	
	result.push(new Tuple<AirplaneValidationFlag, string>(AirplaneValidationFlag.REGISTRATION_CERT, 'AirplaneValidationFlag.REGISTRATION_CERT'))
	
	result.push(new Tuple<AirplaneValidationFlag, string>(AirplaneValidationFlag.FIRST_AID_KIT, 'AirplaneValidationFlag.FIRST_AID_KIT'))
	
	result.push(new Tuple<AirplaneValidationFlag, string>(AirplaneValidationFlag.FIRE_EXTINGUISHER, 'AirplaneValidationFlag.FIRE_EXTINGUISHER'))
	
	result.push(new Tuple<AirplaneValidationFlag, string>(AirplaneValidationFlag.OPERATION_SPEC, 'AirplaneValidationFlag.OPERATION_SPEC'))
	
	result.push(new Tuple<AirplaneValidationFlag, string>(AirplaneValidationFlag.VALID_337_CAMERA, 'AirplaneValidationFlag.VALID_337_CAMERA'))
	
	result.push(new Tuple<AirplaneValidationFlag, string>(AirplaneValidationFlag.VALID_337_EXHAUST, 'AirplaneValidationFlag.VALID_337_EXHAUST'))
	
	result.push(new Tuple<AirplaneValidationFlag, string>(AirplaneValidationFlag.VALID_STC_CAMERA, 'AirplaneValidationFlag.VALID_STC_CAMERA'))
	
	result.push(new Tuple<AirplaneValidationFlag, string>(AirplaneValidationFlag.VALID_STC_EXHAUST, 'AirplaneValidationFlag.VALID_STC_EXHAUST'))
	
	result.push(new Tuple<AirplaneValidationFlag, string>(AirplaneValidationFlag.FLIGHT_LOGBOOK, 'AirplaneValidationFlag.FLIGHT_LOGBOOK'))
	
	result.push(new Tuple<AirplaneValidationFlag, string>(AirplaneValidationFlag.AIR_OPERATOR, 'AirplaneValidationFlag.AIR_OPERATOR'))
	
	result.push(new Tuple<AirplaneValidationFlag, string>(AirplaneValidationFlag.AIRCRAFT_WEIGHT, 'AirplaneValidationFlag.AIRCRAFT_WEIGHT'))
	
	result.push(new Tuple<AirplaneValidationFlag, string>(AirplaneValidationFlag.PROPELLER_OVERHAUL, 'AirplaneValidationFlag.PROPELLER_OVERHAUL'))
	
	result.push(new Tuple<AirplaneValidationFlag, string>(AirplaneValidationFlag.SINGLE_ENGINE_OVERHAUL, 'AirplaneValidationFlag.SINGLE_ENGINE_OVERHAUL'))
	
	result.push(new Tuple<AirplaneValidationFlag, string>(AirplaneValidationFlag.LEFT_ENGINE_OVERHAUL, 'AirplaneValidationFlag.LEFT_ENGINE_OVERHAUL'))
	
	result.push(new Tuple<AirplaneValidationFlag, string>(AirplaneValidationFlag.RIGHT_ENGINE_OVERHAUL, 'AirplaneValidationFlag.RIGHT_ENGINE_OVERHAUL'))
	
	result.push(new Tuple<AirplaneValidationFlag, string>(AirplaneValidationFlag.LEFT_PROPELLER_OVERHAUL, 'AirplaneValidationFlag.LEFT_PROPELLER_OVERHAUL'))
	
	result.push(new Tuple<AirplaneValidationFlag, string>(AirplaneValidationFlag.RIGHT_PROPELLER_OVERHAUL, 'AirplaneValidationFlag.RIGHT_PROPELLER_OVERHAUL'))
	
	result.push(new Tuple<AirplaneValidationFlag, string>(AirplaneValidationFlag.CVFR_ATS, 'AirplaneValidationFlag.CVFR_ATS'))
	
	result.push(new Tuple<AirplaneValidationFlag, string>(AirplaneValidationFlag.WEIGHT_BALANCE_FORM, 'AirplaneValidationFlag.WEIGHT_BALANCE_FORM'))
	
	result.push(new Tuple<AirplaneValidationFlag, string>(AirplaneValidationFlag.CHECK_LIST, 'AirplaneValidationFlag.CHECK_LIST'))
	
	result.push(new Tuple<AirplaneValidationFlag, string>(AirplaneValidationFlag.NOISE_CERT, 'AirplaneValidationFlag.NOISE_CERT'))
	

    return result;
}

