
import { Injectable, Inject } from '@angular/core';
import { RestUtil, EntityResponse, EntitiesResponse, ActionResponse } from '../utils';
import { GooxConfig } from '../config';

import { LoginParams } from '.';
import { User } from '.';



// UserEndPoint Services for user registration and login 
@Injectable()
export class UserEndPoint {

  // URL to web api
  private baseUrl = '/user';

  // Class constructor
  constructor(@Inject('config') private config: GooxConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Authorize user, verify user exists in the system
   * The response includes access token valid for 20 minutes. The client side should renew the token before expiration using refresh-token method
   */
  authorize(body?: LoginParams) {
    return this.rest.post<EntityResponse<User>>(`${this.baseUrl}/authorize`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

}
