
import { AccountStatusCode } from '.';
import { Address } from '.';
import { ValidationFlag } from '.';
import { BaseEntity } from '.';



// Contact entity is a billing account in the system
export class Contact extends BaseEntity {
 
    // First name 
    public firstName: string;
 
    // Last name 
    public lastName: string;
 
    // Contact description 
    public description: string;
 
    // Title in the account 
    public title: string;
 
    // Contact birthday 
    public birthDay: number;
 
    // Contact status: UNDEFINED | ACTIVE | INACTIVE | BLOCKED | SUSPENDE 
    public status: AccountStatusCode;
 
    // Office / Landline phone 
    public phone: string;
 
    // Office / Landline fax 
    public fax: string;
 
    // Mobile phone 
    public mobile: string;
 
    // Primary address 
    public primaryAddress: Address;
 
    // Total single engine hours 
    public singleEngineHours: number;
 
    // Total dual engine hours 
    public dualEngineHours: number;
 
    // Total flight hours 
    public totalFlightHours: number;
 
    // Contact groups 
    public groups: string[];
 
    // ID number 
    public idNumber: string;
 
    // Start to fly on [epoch time milliseconds] 
    public startToFlyDate: number;
 
    // Authorized to fly by user at time 
    public authorizedToFlyBy: string;
 
    // Authorized photographer by user at timeValidations 
    public authorizedPhotographyBy: string;
 
    // Pilot license number 
    public pilotLicense: string;
 
    // Pilot license type 
    public licenseTypes: string[];
 
    // Pilot license group 
    public licenseGroup: string;
 
    // Medical certificate class 
    public medicalCertClass: string;
 
    // Enum flags - list of certifications and validations 
    public validations: ValidationFlag;
 
    // Flag indicating if this contact is a pilot 
    public isPilot: boolean;
 
 
}



