



// Tuple is a generic key-value pair
export class Tuple<K,T> {
 
    // Generic key 
    public key: K;
 
    // Generic value 
    public value: T;
 
     constructor(key?: K, value?: T) { 
        if (key !== undefined) { this.key = key; }
        if (value !== undefined) { this.value = value; }
    }

}



