
import { Tuple } from '.';


// UserStatusCode represents the user status: PENDING | ACTIVE | BLOCKED ...
export enum UserStatusCode {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // User is registered and pending verification [1] 
    PENDING = 1,
 
    // Active user in the system [2] 
    ACTIVE = 2,
 
    // Blocked user (only account system can unblock the user) [3] 
    BLOCKED = 3,
 
    // Suspended user (about to be deleted) [4] 
    SUSPENDED = 4,
 
}

// Return list of UserStatusCode values and their display names
export function GetUserStatusCodes() : Tuple<UserStatusCode, string>[] {
	let result : Tuple<UserStatusCode, string>[] = [];

    
	result.push(new Tuple<UserStatusCode, string>(UserStatusCode.PENDING, 'UserStatusCode.PENDING'))
	
	result.push(new Tuple<UserStatusCode, string>(UserStatusCode.ACTIVE, 'UserStatusCode.ACTIVE'))
	
	result.push(new Tuple<UserStatusCode, string>(UserStatusCode.BLOCKED, 'UserStatusCode.BLOCKED'))
	
	result.push(new Tuple<UserStatusCode, string>(UserStatusCode.SUSPENDED, 'UserStatusCode.SUSPENDED'))
	

    return result;
}

// Return map of UserStatusCode values and their display names
export function MapUserStatusCodes() : Map<UserStatusCode, string> {
    let result = new Map<UserStatusCode, string>();

    
	result.set(UserStatusCode.UNDEFINED, 'Undefined');
	
	result.set(UserStatusCode.PENDING, 'Pending');
	
	result.set(UserStatusCode.ACTIVE, 'Active');
	
	result.set(UserStatusCode.BLOCKED, 'Blocked');
	
	result.set(UserStatusCode.SUSPENDED, 'Suspended');
	

    return result;
}
