
import { Address } from '.';
import { LeadStatusCode } from '.';
import { BaseEntity } from '.';



// Lead entity is a lead in the CRM system
export class Lead extends BaseEntity {
 
    // First name 
    public firstName: string;
 
    // Last name 
    public lastName: string;
 
    // Account description 
    public description: string;
 
    // Office / Landline phone 
    public phone: string;
 
    // Office / Landline fax 
    public fax: string;
 
    // Mobile phone 
    public mobile: string;
 
    // Primary address 
    public primaryAddress: Address;
 
    // Lead source 
    public leadSource: string;
 
    // Lead source description 
    public leadSourceDescription: string;
 
    // Campaign Id 
    public campaignId: string;
 
    // Account status: UNDEFINED | ACTIVE | INACTIVE | BLOCKED | SUSPENDED 
    public status: LeadStatusCode;
 
    // Referred by 
    public referredBy: string;
 
    // Notes 
    public notes: string;
 
    // Assigned to user 
    public assignedTo: string;
 
    // 
    public price: number;
 
    // 
    public commission: number;
 
    // 
    public totalCost: number;
 
    // 
    public commissionPaid: boolean;
 
 
}



