
import { CampaignTypeCode } from '.';
import { CampaignStatusCode } from '.';
import { CurrencyCode } from '.';
import { BaseEntity } from '.';



// Campaign entity represents a marketing campaign in the system
export class Campaign extends BaseEntity {
 
    // Campaign name 
    public name: string;
 
    // Campaign type: UNDEFINED | WEB | MAIL | TELE 
    public type: CampaignTypeCode;
 
    // Campaign status: UNDEFINED | PLANNED | ACTIVE | COMPLETED 
    public status: CampaignStatusCode;
 
    // Campaign content 
    public content: string;
 
    // Campaign start date 
    public startDate: number;
 
    // Campaign end date 
    public endDate: number;
 
    // Campaign budget 
    public budget: number;
 
    // Base rate 
    public baseRate: number;
 
    // Refer URL 
    public referUrl: string;
 
    // Actual campaign cost 
    public expectedCost: number;
 
    // Actual campaign cost 
    public actualCost: number;
 
    // Currency code 
    public currency: CurrencyCode;
 
 
}



