
import { Injectable, Inject } from '@angular/core';
import { RestUtil, EntityResponse, EntitiesResponse, ActionResponse } from '../utils';
import { GooxConfig } from '../config';

import { TimeSeries } from '.';
import { AuditLog } from '.';



// AuditLogsEndPoint Services for auditLogs actions 
@Injectable()
export class AuditLogsEndPoint {

  // URL to web api
  private baseUrl = '/audit_logs';

  // Class constructor
  constructor(@Inject('config') private config: GooxConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Create new auditLog
   */
  create(body?: AuditLog) {
    return this.rest.post<EntityResponse<AuditLog>>(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Get a single auditLog by id
   */
  get(id?: string) {
    return this.rest.get<EntityResponse<AuditLog>>(`${this.baseUrl}/${id}`);
  }

  /**
   * Find auditLogs by query
   */
  find(from?: number, to?: number, userId?: string, action?: string, itemType?: string, itemId?: string, itemName?: string, search?: string, sort?: string, page?: number, size?: number) {
    const params = [];		
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }
    if (userId != null) { params.push(`userId=${userId}`); }
    if (action != null) { params.push(`action=${action}`); }
    if (itemType != null) { params.push(`itemType=${itemType}`); }
    if (itemId != null) { params.push(`itemId=${itemId}`); }
    if (itemName != null) { params.push(`itemName=${itemName}`); }
    if (search != null) { params.push(`search=${search}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (size != null) { params.push(`size=${size}`); }

		return this.rest.get<EntitiesResponse<AuditLog>>(`${this.baseUrl}`, ...params);
  }

  /**
   * Find auditLogs count histogram over time
   */
  histogram(from?: number, to?: number, userId?: string, action?: string, itemType?: string, itemId?: string, itemName?: string, search?: string, sort?: string, page?: number, size?: number) {
    const params = [];		
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }
    if (userId != null) { params.push(`userId=${userId}`); }
    if (action != null) { params.push(`action=${action}`); }
    if (itemType != null) { params.push(`itemType=${itemType}`); }
    if (itemId != null) { params.push(`itemId=${itemId}`); }
    if (itemName != null) { params.push(`itemName=${itemName}`); }
    if (search != null) { params.push(`search=${search}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (size != null) { params.push(`size=${size}`); }

		return this.rest.get<EntityResponse<TimeSeries<number>>>(`${this.baseUrl}/histogram`, ...params);
  }

}
