



// Magneto model represents magneto metrics
export class Magneto {
 
    // Inspection frequency [hours] 
    public inspectionFrequency: number;
 
    // Last Inspection [hours] 
    public lastInspectionHours: number;
 
    // Next Inspection [hours] 
    public nextInspectionHours: number;
 
    // Remaining hours to the next inspection 
    public remainingHours: number;
 
     constructor(inspectionFrequency?: number, lastInspectionHours?: number, nextInspectionHours?: number, remainingHours?: number) { 
        if (inspectionFrequency !== undefined) { this.inspectionFrequency = inspectionFrequency; }
        if (lastInspectionHours !== undefined) { this.lastInspectionHours = lastInspectionHours; }
        if (nextInspectionHours !== undefined) { this.nextInspectionHours = nextInspectionHours; }
        if (remainingHours !== undefined) { this.remainingHours = remainingHours; }
    }

}



