
import { Injectable, Inject } from '@angular/core';
import { RestUtil, EntityResponse, EntitiesResponse, ActionResponse } from '../utils';
import { GooxConfig } from '../config';

import { Lead } from '.';



// LeadsEndPoint Services for leads actions 
@Injectable()
export class LeadsEndPoint {

  // URL to web api
  private baseUrl = '/leads';

  // Class constructor
  constructor(@Inject('config') private config: GooxConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Create new lead
   */
  create(body?: Lead) {
    return this.rest.post<EntityResponse<Lead>>(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Update existing lead
   */
  update(body?: Lead) {
    return this.rest.put<EntityResponse<Lead>>(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Delete lead and all its content
   */
  delete(id?: string) {
    return this.rest.delete<ActionResponse>(`${this.baseUrl}/${id}`);
  }

  /**
   * Get a single lead by id
   */
  get(id?: string) {
    return this.rest.get<EntityResponse<Lead>>(`${this.baseUrl}/${id}`);
  }

  /**
   * Find leads by query
   */
  find(search?: string, sort?: string, page?: number, size?: number) {
    const params = [];		
    if (search != null) { params.push(`search=${search}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (size != null) { params.push(`size=${size}`); }

		return this.rest.get<EntitiesResponse<Lead>>(`${this.baseUrl}`, ...params);
  }

}
