



// Fuel model represents fuel metrics
export class Fuel {
 
    // Fuel tank size [Liters] 
    public totalTime: number;
 
    // Fuel consumption 
    public consumption: number;
 
    // Fuel cost 
    public cost: number;
 
    // Estimated fuel in tank 
    public estimatedFuelInTank: number;
 
    // Estimated endurance 
    public estimatedEndurance: number;
 
    // Last updated on 
    public lastUpdatedOn: number;
 
    // Last updated by flight 
    public lastUpdatedByFlight: number;
 
     constructor(tankSize?: number, consumption?: number, cost?: number, estimatedFuelInTank?: number, estimatedEndurance?: number, lastUpdatedOn?: number, lastUpdatedByFlight?: number) { 
        if (tankSize !== undefined) { this.totalTime = tankSize; }
        if (consumption !== undefined) { this.consumption = consumption; }
        if (cost !== undefined) { this.cost = cost; }
        if (estimatedFuelInTank !== undefined) { this.estimatedFuelInTank = estimatedFuelInTank; }
        if (estimatedEndurance !== undefined) { this.estimatedEndurance = estimatedEndurance; }
        if (lastUpdatedOn !== undefined) { this.lastUpdatedOn = lastUpdatedOn; }
        if (lastUpdatedByFlight !== undefined) { this.lastUpdatedByFlight = lastUpdatedByFlight; }
    }

}



