
import { Tuple } from '.';


// ValidationFlag represents combination of validations
export enum ValidationFlag {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // Valid medical certificate  [1] 
    MEDICAL_CERT = 1,
 
    // Valid pilot license [2] 
    PILOT_LICENSE = 2,
 
    // Single Engine Certificate [4] 
    SINGLE_ENG_CERT = 4,
 
    // Dual Engine Certificate [8] 
    DUAL_ENG_CERT = 8,
 
    // 666 Instruments Certificate [16] 
    INST_666_CERT = 16,
 
    // REC Instruments Certificate [32] 
    INST_REC_CERT = 32,
 
    // Day time fly [64] 
    DAYTIME_FLY = 64,
 
    // Nighttime fly [128] 
    NIGHTTIME_FLY = 128,
 
    // Night CVFR [256] 
    NIGHT_CVFR_CUR = 256,
 
    // Route exam [512] 
    ROUTE_EXAM = 512,
 
    // DI Certificate [1024] 
    DI_CERT = 1024,
 
    // International [2048] 
    INTERNATIONAL = 2048,
 
    // English Proficiency [4096] 
    ENGLISH_PROF = 4096,
 
    // Instructor license [8192] 
    INSTRUCTOR_LICENSE = 8192,
 
    // Company check ? [16384] 
    COMPANY_CHECK = 16384,
 
    // Company Examiner ? [32768] 
    COMPANY_EXAMIN = 32768,
 
    // Commercial 172p [65536] 
    COMMERCIAL_172P = 65536,
 
    // Commercial 172xp [131072] 
    COMMERCIAL_172XP = 131072,
 
    // Commercial 172xp [262144] 
    COMMERCIAL_207 = 262144,
 
}

// Return list of ValidationFlag values and their display names
export function GetValidationFlags() : Tuple<ValidationFlag, string>[] {
	let result : Tuple<ValidationFlag, string>[] = [];

    
	result.push(new Tuple<ValidationFlag, string>(ValidationFlag.MEDICAL_CERT, 'ValidationFlag.MEDICAL_CERT'))
	
	result.push(new Tuple<ValidationFlag, string>(ValidationFlag.PILOT_LICENSE, 'ValidationFlag.PILOT_LICENSE'))
	
	result.push(new Tuple<ValidationFlag, string>(ValidationFlag.SINGLE_ENG_CERT, 'ValidationFlag.SINGLE_ENG_CERT'))
	
	result.push(new Tuple<ValidationFlag, string>(ValidationFlag.DUAL_ENG_CERT, 'ValidationFlag.DUAL_ENG_CERT'))
	
	result.push(new Tuple<ValidationFlag, string>(ValidationFlag.INST_666_CERT, 'ValidationFlag.INST_666_CERT'))
	
	result.push(new Tuple<ValidationFlag, string>(ValidationFlag.INST_REC_CERT, 'ValidationFlag.INST_REC_CERT'))
	
	result.push(new Tuple<ValidationFlag, string>(ValidationFlag.DAYTIME_FLY, 'ValidationFlag.DAYTIME_FLY'))
	
	result.push(new Tuple<ValidationFlag, string>(ValidationFlag.NIGHTTIME_FLY, 'ValidationFlag.NIGHTTIME_FLY'))
	
	result.push(new Tuple<ValidationFlag, string>(ValidationFlag.NIGHT_CVFR_CUR, 'ValidationFlag.NIGHT_CVFR_CUR'))
	
	result.push(new Tuple<ValidationFlag, string>(ValidationFlag.ROUTE_EXAM, 'ValidationFlag.ROUTE_EXAM'))
	
	result.push(new Tuple<ValidationFlag, string>(ValidationFlag.DI_CERT, 'ValidationFlag.DI_CERT'))
	
	result.push(new Tuple<ValidationFlag, string>(ValidationFlag.INTERNATIONAL, 'ValidationFlag.INTERNATIONAL'))
	
	result.push(new Tuple<ValidationFlag, string>(ValidationFlag.ENGLISH_PROF, 'ValidationFlag.ENGLISH_PROF'))
	
	result.push(new Tuple<ValidationFlag, string>(ValidationFlag.INSTRUCTOR_LICENSE, 'ValidationFlag.INSTRUCTOR_LICENSE'))
	
	result.push(new Tuple<ValidationFlag, string>(ValidationFlag.COMPANY_CHECK, 'ValidationFlag.COMPANY_CHECK'))
	
	result.push(new Tuple<ValidationFlag, string>(ValidationFlag.COMPANY_EXAMIN, 'ValidationFlag.COMPANY_EXAMIN'))
	
	result.push(new Tuple<ValidationFlag, string>(ValidationFlag.COMMERCIAL_172P, 'ValidationFlag.COMMERCIAL_172P'))
	
	result.push(new Tuple<ValidationFlag, string>(ValidationFlag.COMMERCIAL_172XP, 'ValidationFlag.COMMERCIAL_172XP'))
	
	result.push(new Tuple<ValidationFlag, string>(ValidationFlag.COMMERCIAL_207, 'ValidationFlag.COMMERCIAL_207'))
	

    return result;
}

// Return map of ValidationFlag values and their display names
export function MapValidationFlags() : Map<ValidationFlag, string> {
    let result = new Map<ValidationFlag, string>();

    
	result.set(ValidationFlag.UNDEFINED, 'Undefined');
	
	result.set(ValidationFlag.MEDICAL_CERT, 'Medical Cert');
	
	result.set(ValidationFlag.PILOT_LICENSE, 'Pilot License');
	
	result.set(ValidationFlag.SINGLE_ENG_CERT, 'Single Eng Cert');
	
	result.set(ValidationFlag.DUAL_ENG_CERT, 'Dual Eng Cert');
	
	result.set(ValidationFlag.INST_666_CERT, 'Inst 666 Cert');
	
	result.set(ValidationFlag.INST_REC_CERT, 'Inst Rec Cert');
	
	result.set(ValidationFlag.DAYTIME_FLY, 'Daytime Fly');
	
	result.set(ValidationFlag.NIGHTTIME_FLY, 'Nighttime Fly');
	
	result.set(ValidationFlag.NIGHT_CVFR_CUR, 'Night Cvfr Cur');
	
	result.set(ValidationFlag.ROUTE_EXAM, 'Route Exam');
	
	result.set(ValidationFlag.DI_CERT, 'Di Cert');
	
	result.set(ValidationFlag.INTERNATIONAL, 'International');
	
	result.set(ValidationFlag.ENGLISH_PROF, 'English Prof');
	
	result.set(ValidationFlag.INSTRUCTOR_LICENSE, 'Instructor License');
	
	result.set(ValidationFlag.COMPANY_CHECK, 'Company Check');
	
	result.set(ValidationFlag.COMPANY_EXAMIN, 'Company Examin');
	
	result.set(ValidationFlag.COMMERCIAL_172P, 'Commercial 172p');
	
	result.set(ValidationFlag.COMMERCIAL_172XP, 'Commercial 172xp');
	
	result.set(ValidationFlag.COMMERCIAL_207, 'Commercial 207');
	

    return result;
}
