
import { TimeFrame } from '.';
import { TimeDataPoint } from '.';



// TimeSeries is a set of data points over time
export class TimeSeries<T> {
 
    // Name of the time series 
    public name: string;
 
    // Range of the series (from ... to) 
    public range: TimeFrame;
 
    // Series data points 
    public values: TimeDataPoint<T>[];
 
     constructor(name?: string, range?: TimeFrame, values?: TimeDataPoint<T>[]) { 
        if (name !== undefined) { this.name = name; }
        if (range !== undefined) { this.range = range; }
        if (values !== undefined) { this.values = values; }
    }

}



