
import { Tuple } from '.';


// CampaignTypeCode represents the campaign type: WEB | EMAIL | TELESALES
export enum CampaignTypeCode {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // Web [1] 
    WEB = 1,
 
    // Mail [2] 
    EMAIL = 2,
 
    // Telephone Sales [3] 
    TELESALES = 3,
 
}

// Return list of CampaignTypeCode values and their display names
export function GetCampaignTypeCodes() : Tuple<CampaignTypeCode, string>[] {
	let result : Tuple<CampaignTypeCode, string>[] = [];

    
	result.push(new Tuple<CampaignTypeCode, string>(CampaignTypeCode.WEB, 'CampaignTypeCode.WEB'))
	
	result.push(new Tuple<CampaignTypeCode, string>(CampaignTypeCode.EMAIL, 'CampaignTypeCode.EMAIL'))
	
	result.push(new Tuple<CampaignTypeCode, string>(CampaignTypeCode.TELESALES, 'CampaignTypeCode.TELESALES'))
	

    return result;
}

// Return map of CampaignTypeCode values and their display names
export function MapCampaignTypeCodes() : Map<CampaignTypeCode, string> {
    let result = new Map<CampaignTypeCode, string>();

    
	result.set(CampaignTypeCode.UNDEFINED, 'Undefined');
	
	result.set(CampaignTypeCode.WEB, 'Web');
	
	result.set(CampaignTypeCode.EMAIL, 'Email');
	
	result.set(CampaignTypeCode.TELESALES, 'Telesales');
	

    return result;
}
