
import { Injectable, Inject } from '@angular/core';
import { RestUtil, EntityResponse, EntitiesResponse, ActionResponse } from '../utils';
import { GooxConfig } from '../config';

import { Document } from '.';



// DocumentsEndPoint Services for documents actions 
@Injectable()
export class DocumentsEndPoint {

  // URL to web api
  private baseUrl = '/documents';

  // Class constructor
  constructor(@Inject('config') private config: GooxConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Create new document
   */
  create(body?: Document) {
    return this.rest.post<EntityResponse<Document>>(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Update existing document
   */
  update(body?: Document) {
    return this.rest.put<EntityResponse<Document>>(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Delete document and all its content
   */
  delete(id?: string) {
    return this.rest.delete<ActionResponse>(`${this.baseUrl}/${id}`);
  }

  /**
   * Get a single document by id
   */
  get(id?: string) {
    return this.rest.get<EntityResponse<Document>>(`${this.baseUrl}/${id}`);
  }

  /**
   * Find documents by query
   */
  find(search?: string, sort?: string, page?: number, size?: number) {
    const params = [];		
    if (search != null) { params.push(`search=${search}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (size != null) { params.push(`size=${size}`); }

		return this.rest.get<EntitiesResponse<Document>>(`${this.baseUrl}`, ...params);
  }

}
