
import { BaseEntity } from '.';



// Document entity represents a document in the system
export class Document extends BaseEntity {
 
    // Document name 
    public name: string;
 
    // Document URL 
    public url: string;
 
    // Document creator 
    public createdBy: string;
 
    // Document active from [epoch time milliseconds] 
    public activeFrom: number;
 
    // Document active until [epoch time milliseconds] 
    public activeUntil: number;
 
    // Document category 
    public category: string;
 
 
}



