
import { Injectable, Inject } from '@angular/core';
import { RestUtil, EntityResponse, EntitiesResponse, ActionResponse } from '../utils';
import { GooxConfig } from '../config';

import { Contact } from '.';



// ContactsEndPoint Services for contacts actions 
@Injectable()
export class ContactsEndPoint {

  // URL to web api
  private baseUrl = '/contacts';

  // Class constructor
  constructor(@Inject('config') private config: GooxConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Create new contact
   */
  create(body?: Contact) {
    return this.rest.post<EntityResponse<Contact>>(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Update existing contact
   */
  update(body?: Contact) {
    return this.rest.put<EntityResponse<Contact>>(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Delete contact and all its content
   */
  delete(id?: string) {
    return this.rest.delete<ActionResponse>(`${this.baseUrl}/${id}`);
  }

  /**
   * Get a single contact by id
   */
  get(id?: string) {
    return this.rest.get<EntityResponse<Contact>>(`${this.baseUrl}/${id}`);
  }

  /**
   * Find contacts by query
   */
  find(search?: string, sort?: string, page?: number, size?: number) {
    const params = [];		
    if (search != null) { params.push(`search=${search}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (size != null) { params.push(`size=${size}`); }

		return this.rest.get<EntitiesResponse<Contact>>(`${this.baseUrl}`, ...params);
  }

}
