
import { Magneto } from '.';
import { Propeller } from '.';
import { Fuel } from '.';
import { BaseEntity } from '.';
import { AirplaneStatusCode } from '.';
import { AirplaneValidationFlag } from '.';
import { Engine } from '.';



// Airplane entity represents an airplane
export class Airplane extends BaseEntity {
 
    // Name 
    public name: string;
 
    // Description 
    public description: string;
 
    // Airplane model 
    public model: string;
 
    // Airplane serial number 
    public serialNo: string;
 
    // Airplane status 
    public status: AirplaneStatusCode;
 
    // Airplane hobs counter 
    public hobs: number;
 
    // Airplane tachometer counter 
    public tach: number;
 
    // Airplane next tachometer for inspection 
    public nextTach: number;
 
    // Airplane air time (hours) 
    public airTime: number;
 
    // Airplane customer number 
    public cstomerNo: string;
 
    // Airworthiness flags 
    public airworthiness: AirplaneValidationFlag;
 
    // Fixed value to first flight 
    public fixedValueToFirstFlight: number;
 
    // AC total time 
    public acTotalTime: number;
 
    // Single / Left Engine indicators 
    public engine: Engine;
 
    // Left Magneto indicators 
    public leftMagneto: Magneto;
 
    // Right Magneto indicators 
    public rightMagneto: Magneto;
 
    // Single / Left Propeller indicators 
    public propeller: Propeller;
 
    // Fuel indicators 
    public fuel: Fuel;
 
 
}



