
import { Injectable, Inject } from '@angular/core';
import { RestUtil, EntityResponse, EntitiesResponse, ActionResponse } from '../utils';
import { GooxConfig } from '../config';

import { Account } from '.';



// AccountsEndPoint Services for user registration and login 
@Injectable()
export class AccountsEndPoint {

  // URL to web api
  private baseUrl = '/accounts';

  // Class constructor
  constructor(@Inject('config') private config: GooxConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Create new account
   */
  create(body?: Account) {
    return this.rest.post<EntityResponse<Account>>(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Update existing account
   */
  update(body?: Account) {
    return this.rest.put<EntityResponse<Account>>(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Delete account and all its content
   */
  delete(id?: string) {
    return this.rest.delete<ActionResponse>(`${this.baseUrl}/${id}`);
  }

  /**
   * Get a single account by id
   */
  get(id?: string) {
    return this.rest.get<EntityResponse<Account>>(`${this.baseUrl}/${id}`);
  }

  /**
   * Find accounts by query
   */
  find(search?: string, status?: [], sort?: string, page?: number, size?: number) {
    const params = [];		
    if (search != null) { params.push(`search=${search}`); }
    if (status != null) { params.push(`status=${status}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (size != null) { params.push(`size=${size}`); }

		return this.rest.get<EntitiesResponse<Account>>(`${this.baseUrl}`, ...params);
  }

}
