
import { Tuple } from '.';


// CampaignStatusCode represents the campaign status: PLANNING | ACTIVE | COMPLETED ...
export enum CampaignStatusCode {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // Planned campaign [1] 
    PLANNING = 1,
 
    // Active campaign [2] 
    ACTIVE = 2,
 
    // `Completed campaign [3] 
    COMPLETED = 3,
 
    // Suspended (about to be deleted) [4] 
    SUSPENDED = 4,
 
}

// Return list of CampaignStatusCode values and their display names
export function GetCampaignStatusCodes() : Tuple<CampaignStatusCode, string>[] {
	let result : Tuple<CampaignStatusCode, string>[] = [];

    
	result.push(new Tuple<CampaignStatusCode, string>(CampaignStatusCode.PLANNING, 'CampaignStatusCode.PLANNING'))
	
	result.push(new Tuple<CampaignStatusCode, string>(CampaignStatusCode.ACTIVE, 'CampaignStatusCode.ACTIVE'))
	
	result.push(new Tuple<CampaignStatusCode, string>(CampaignStatusCode.COMPLETED, 'CampaignStatusCode.COMPLETED'))
	
	result.push(new Tuple<CampaignStatusCode, string>(CampaignStatusCode.SUSPENDED, 'CampaignStatusCode.SUSPENDED'))
	

    return result;
}

// Return map of CampaignStatusCode values and their display names
export function MapCampaignStatusCodes() : Map<CampaignStatusCode, string> {
    let result = new Map<CampaignStatusCode, string>();

    
	result.set(CampaignStatusCode.UNDEFINED, 'Undefined');
	
	result.set(CampaignStatusCode.PLANNING, 'Planning');
	
	result.set(CampaignStatusCode.ACTIVE, 'Active');
	
	result.set(CampaignStatusCode.COMPLETED, 'Completed');
	
	result.set(CampaignStatusCode.SUSPENDED, 'Suspended');
	

    return result;
}
