
import { Tuple } from '.';


// CurrencyCode represents the currency: ILS | USD | EUR ...
export enum CurrencyCode {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // ILS [1] 
    ILS = 1,
 
    // USD [2] 
    USD = 2,
 
    // EURO [3] 
    EUR = 3,
 
}

// Return list of CurrencyCode values and their display names
export function GetCurrencyCodes() : Tuple<CurrencyCode, string>[] {
	let result : Tuple<CurrencyCode, string>[] = [];

    
	result.push(new Tuple<CurrencyCode, string>(CurrencyCode.ILS, 'CurrencyCode.ILS'))
	
	result.push(new Tuple<CurrencyCode, string>(CurrencyCode.USD, 'CurrencyCode.USD'))
	
	result.push(new Tuple<CurrencyCode, string>(CurrencyCode.EUR, 'CurrencyCode.EUR'))
	

    return result;
}

// Return map of CurrencyCode values and their display names
export function MapCurrencyCodes() : Map<CurrencyCode, string> {
    let result = new Map<CurrencyCode, string>();

    
	result.set(CurrencyCode.UNDEFINED, 'Undefined');
	
	result.set(CurrencyCode.ILS, 'Ils');
	
	result.set(CurrencyCode.USD, 'Usd');
	
	result.set(CurrencyCode.EUR, 'Eur');
	

    return result;
}
