
import { AccountStatusCode } from '.';
import { Address } from '.';
import { BaseEntity } from '.';



// Account entity is a billing account in the system
export class Account extends BaseEntity {
 
    // Account name 
    public name: string;
 
    // Account description 
    public description: string;
 
    // Account status: UNDEFINED | ACTIVE | INACTIVE | BLOCKED | SUSPENDED 
    public status: AccountStatusCode;
 
    // Office / Landline phone 
    public phone: string;
 
    // Office / Landline fax 
    public fax: string;
 
    // Mobile phone 
    public mobile: string;
 
    // Billing address 
    public billingAddress: Address;
 
    // Shipping address 
    public shippingAddress: Address;
 
    // Campaign Id 
    public campaignId: string;
 
    // Is credit enabled for this account 
    public enableCredit: boolean;
 
    // Current account credit 
    public currentCredit: number;
 
    // Base rate for billing 
    public baseRate: number;
 
    // Permanent discount (percentage) 
    public percentageDiscount: number;
 
    // Green invoice Id 
    public invoiceId: string;
 
 
}



