



// Propeller model represents propeller metrics
export class Propeller {
 
    // Propeller total time [hours] 
    public totalTime: number;
 
    // Overhaul time frequency  [hours] 
    public overhaulFrequency: number;
 
    // Overhaul next due [hours] 
    public overhaulNextDue: number;
 
    // Time remaining till the next Overhaul [hours] 
    public hoursToOverhaul: number;
 
    // Last airframe Overhaul [hours] 
    public lastAirframeOverhaul: number;
 
     constructor(totalTime?: number, overhaulFrequency?: number, overhaulNextDue?: number, hoursToOverhaul?: number, lastAirframeOverhaul?: number) { 
        if (totalTime !== undefined) { this.totalTime = totalTime; }
        if (overhaulFrequency !== undefined) { this.overhaulFrequency = overhaulFrequency; }
        if (overhaulNextDue !== undefined) { this.overhaulNextDue = overhaulNextDue; }
        if (hoursToOverhaul !== undefined) { this.hoursToOverhaul = hoursToOverhaul; }
        if (lastAirframeOverhaul !== undefined) { this.lastAirframeOverhaul = lastAirframeOverhaul; }
    }

}



