
import { Injectable, Inject } from '@angular/core';
import { RestUtil, EntityResponse, EntitiesResponse, ActionResponse } from '../utils';
import { GooxConfig } from '../config';

import { User } from '.';



// UsersEndPoint Services for users actions 
@Injectable()
export class UsersEndPoint {

  // URL to web api
  private baseUrl = '/users';

  // Class constructor
  constructor(@Inject('config') private config: GooxConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Create new user
   */
  create(body?: User) {
    return this.rest.post<EntityResponse<User>>(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Update existing user
   */
  update(body?: User) {
    return this.rest.put<EntityResponse<User>>(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Delete user and all its content
   */
  delete(id?: string) {
    return this.rest.delete<ActionResponse>(`${this.baseUrl}/${id}`);
  }

  /**
   * Get a single user by id
   */
  get(id?: string) {
    return this.rest.get<EntityResponse<User>>(`${this.baseUrl}/${id}`);
  }

  /**
   * Find users by query
   */
  find(search?: string, sort?: string, page?: number, size?: number) {
    const params = [];		
    if (search != null) { params.push(`search=${search}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (size != null) { params.push(`size=${size}`); }

		return this.rest.get<EntitiesResponse<User>>(`${this.baseUrl}`, ...params);
  }

}
