
import { Tuple } from '.';


// AirplaneStatusCode represents the airplane status: ACTIVE | INACTIVE | MAINTENANCE ...
export enum AirplaneStatusCode {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // Active airplane [1] 
    ACTIVE = 1,
 
    // Inactive airplane [2] 
    INACTIVE = 2,
 
}

// Return list of AirplaneStatusCode values and their display names
export function GetAirplaneStatusCodes() : Tuple<AirplaneStatusCode, string>[] {
	let result : Tuple<AirplaneStatusCode, string>[] = [];

    
	result.push(new Tuple<AirplaneStatusCode, string>(AirplaneStatusCode.ACTIVE, 'AirplaneStatusCode.ACTIVE'))
	
	result.push(new Tuple<AirplaneStatusCode, string>(AirplaneStatusCode.INACTIVE, 'AirplaneStatusCode.INACTIVE'))
	

    return result;
}

// Return map of AirplaneStatusCode values and their display names
export function MapAirplaneStatusCodes() : Map<AirplaneStatusCode, string> {
    let result = new Map<AirplaneStatusCode, string>();

    
	result.set(AirplaneStatusCode.UNDEFINED, 'Undefined');
	
	result.set(AirplaneStatusCode.ACTIVE, 'Active');
	
	result.set(AirplaneStatusCode.INACTIVE, 'Inactive');
	

    return result;
}
